@import '../style-base.scss';
@import './base-theme.scss';

.deni-react-treeview-container.orange {
  border-color: lighten($orange, 2%);

  .deni-react-treeview-item-container.orange {
    @include set-theme($orange);

    .icon-and-text {
      .icon {
        background-image: $closed-folder-icon-orange-16;

        &.isleaf {
          background-image: url('data:image/png;base64,');
        }

        &.expanded {
          background-image: $opened-folder-icon-orange-16;
        }
      }
    }

  }

}
