@import '../style-base.scss';
@import './base-theme.scss';

.deni-react-treeview-container.red {
  border-color: lighten($red, 2%);

  .deni-react-treeview-item-container.red {
    @include set-theme($red);

    .icon-and-text {
      .icon {
        background-image: $closed-folder-icon-red-16;

        &.isleaf {
          background-image: url('data:image/png;base64,');
        }

        &.expanded {
          background-image: $opened-folder-icon-red-16;
        }
      }
    }

  }

}
