@import '../style-base.scss';
@import './base-theme.scss';

.deni-react-treeview-container.classic {
  border-color: lighten($blue, 2%);

  .deni-react-treeview-item-container.classic {
    @include set-theme($blue);

    .icon-and-text {
      .icon {
        background-image: $closed-folder-icon-blue-16;

        &.isleaf {
          background-image: url('data:image/png;base64,');
        }

        &.expanded {
          background-image: $opened-folder-icon-blue-16;
        }
      }
    }

  }
}
