/**=====================
     62. Responsive CSS Start
==========================**/
@media screen and (max-width: 1800px) and (min-width: 1660px) {
  .user-status {
    table {
      tbody {
        tr {
          td {
            &:first-child {
              min-width: 285px;
            }
          }
        }
      }
    }
  }
}
@media screen and (max-width: 1880px) and (min-width: 1661px){
  .chart_data_left{
    .card-body{
      .chart-main{
        .media{
          padding: 45px 30px;
          .media-body{
            .right-chart-content{
              margin-left: 10px;
            }
          }
        }
      }
    }
  }     
}
@media screen and (max-width: 1760px) and (min-width: 1661px){
  .ecommerce-widget{
    .progress-showcase{
      margin-top: 10px;
    }
  } 
  .crm-activity{
    > li{
      + li{
        margin-top: 6px;
        padding-top: 6px;
      }
    }
  }
  .testimonial{
    p{
      margin-top: 6px;
      margin-bottom: 6px;
    }
  }
  .offer-slider{
    .selling-slide{
      margin-top: 24px;
    }
  }
  .activity-timeline{
    .media{
      + .media{
        margin-top: 30px;
      }
    }
  }
  .xl-cs-35{
    .card{
      .card-body{
        padding: 34px;
      }
    }
  }
  .product-button{
    .btn{
      padding: 6px 18px;
    }
  }
  .product-social{
    li{
      padding-right: 6px;
    }
  }
  .cal-desc{
    &.card-body{
      padding: 30px;
    }
  }
  .user-status.table-responsive{
    table{
      tr{
        td,th{
          &:first-child{
            min-width: 284px;
          }
          &:nth-child(3){
            min-width: 108px;
          }
        }
      }
    }
  }
  .our-product{
    table{
      tr{
        td{
          padding: 20px 14px 0;
        }
      }
    }
  }
  .appointment-table{
    table{
      tr{
        td{
          &:nth-child(2){
            padding-left: 10px;
          }
        }
      }
    }
  }
  .profile-greeting{
    .greeting-user{
      margin: 30px 0;
    }
  } 
  .earning-card.card{
    .card-body{
      .border-top{
        padding: 40px 20px;
      }
    }
  }  
}
@media (max-width: 1660px) {
  .pre-order{
    order: -1;
  }
  .photos {
    ul {
      li {
        width: 26%;
      }
    }
  }
  .calender-widget {
    .cal-img {
      height: 335px;
    }
  }
  .bar-chart {
    canvas {
      width: 100% !important;
    }
  }

  .chat-box {
    .chat-history {
      .call-icons {
        ul {
          li {
            border: 1px solid $dark-gray;
          }
        }
      }

      .total-time {
        h2 {
          color: $dark-gray;
        }
      }

      .call-content {
        .btn {
          width: auto;
          margin: 0 auto;
        }

        > div {
          height: 545px;
          z-index: 7;
          background-color: rgba(255, 255, 255, 0.75);
          background-blend-mode: overlay;
          width: 100%;
          padding: 30px;
        }
      }

      .receiver-img {
        margin-top: 25px;
      }
    }
  }

  .chat-left-aside {
    .people-list {
      height: 520px;
    }
  }

  .chat-box {
    .chat-right-aside {
      .chat {
        .chat-msg-box {
          height: 440px;
        }
      }
    }

    .people-list {
      ul {
        max-height: 450px;
        overflow: auto;
      }
    }
  }

  .call-chat-body {
    .chat-box {
      .people-list {
        ul {
          max-height: 550px;
          overflow: auto;
        }
      }

      overflow: hidden;
      height: 625px;
    }
  }

  .caller-img {
    position: absolute;
    width: 100%;
    max-width: 100%;
    left: 15px;

    img {
      opacity: 0.7;
    }
  }
}
@media screen and (max-width: 1660px) and (min-width: 1366px) {
  .our-product {
    table {
      tr {
        td {
          padding: 26px 20px 0;
        }
      }
    }
  }
  .calendar-sec {
    .default-datepicker {
      .react-datepicker {
        .react-datepicker__day-name,
        .react-datepicker__day,
        .react-datepicker__time-name {
          line-height: 53px !important;
          width: 53px !important;
        }
      }
    }
  }
}
@media screen and (max-width: 1660px) and (min-width: 1200px) {
  .xl-none {
    display: none;
  }

  .xl-60 {
    max-width: 60%;
    flex: 0 0 60%;
  }

  .xl-50 {
    max-width: 50%;
    flex: 0 0 50%;
  }

  .xl-100 {
    max-width: 100%;
    flex: 0 0 100%;
  }

  .xl-23 {
    max-width: 23%;
    flex: 0 0 23%;
  }

  .xl-25 {
    max-width: 25%;
    flex: 0 0 25%;
  }

  .xl-40 {
    max-width: 40%;
    flex: 0 0 40%;
  }

  .xl-4 {
    max-width: 33.33%;
    flex: 0 0 33.33%;
  }
  .product-page-main {
    .xl-45 {
      max-width: 45%;
      flex: 0 0 45%;
      order: -1;
    }
    .xl-55 {
      max-width: 55%;
      flex: 0 0 55%;
    }
  }
}
