/**=====================
   55. theme layout CSS Start
==========================**/

body {
  .for-dark {
    display: none;
  }
  &[class*="dark-"] {
    .for-light {
      display: none;
    }
    .for-dark {
      display: block;
    }
  }
  &.dark-only {
    color: $dark-all-font-color;
    background-color: $dark-body-background;
    .appointment-sec, .notification{
      .card{
        .card-header{
          border-bottom: 0 !important;
        }
      }
    }
    .login-card{
      .login-main{
        background-color: $dark-body-background;
        .theme-form{
          input{
            &:hover, &:focus, &:active{
              border: 1px solid $dark-card-border;
            }
          }
        }
      }
    } 
    .page-wrapper.only-body{
      background-color: $dark-body-background;
    }
    .small-slick{
      .slick-arrow{
        display: none !important;
      }
    }
    .breadcrumb-colored{
      li{
        .txt-dark, &.txt-dark{
          color: $theme-body-font-color !important;
        }
      }
    }
    .product-social{
      li{
        a{
          border: 1px solid $dark-card-border;
        }
      }
    }
    .react-datepicker__time-container{
      .react-datepicker__time{
        .react-datepicker__time-box{
          ul.react-datepicker__time-list{
            li.react-datepicker__time-list-item{
              &:hover{
                background-color: $dark-card-background;
              }
            }
          }
        }
      }
    }
    .page-wrapper {
      li.list-group-item {
        background-color: unset;
        border: unset;
        color: unset;
        &.active{
          background-color: unset;
        }
      }
      &.compact-wrapper{
        .page-body-wrapper{
          div.sidebar-wrapper{
            .sidebar-main{
              .sidebar-links{
                > li{
                  .sidebar-link{
                    &.active{
                      background-color: rgba($primary-color, 0.2);
                    }
                  }
                }
              }
            }
          }
        }
      }
      .page-header {
        .header-wrapper {
          .nav-right{
            .cart-dropdown{
              li{
                .media{
                  .media-body{
                    > span{
                      color: $dark-all-font-color;
                    }
                  }
                }
              }
            }
          }
          .search-full {
            .form-group{
              &::before{
                color: $dark-all-font-color;
              }
            }
            .ProfileCard {
              border: 1px solid $dark-card-border !important;
            }
          }
        }
      }
    }
    #right-history{
      h6{
        span{
          a{
            color: $dark-all-font-color;
          }
        }
      }
    }
    .customizer-contain {
      color: $dark-body-background;
      .form-control {
        background-color: transparent;
        color: $theme-body-font-color;
        border: 1px solid #ced4da;
      }
    }

    .card {
      background: $dark-card-background;
    }

    .b-r-light {
      border-right: 1px solid $dark-card-border !important;
    }

    .btn {
      color: $dark-all-font-color;
    }

    .txt-dark {
      color: $white !important;
    }
    .user-status {
      table {
        tbody {
          tr {
            td {
              a{
                color: $dark-all-font-color;
              }
            }
          }
        }
      }
    }
    .crm-activity{
      ul.dates{
        li{
          + li{
            border-left: 1px solid $dark-card-border;
          }
        }
      }
    }
    .btn-outline-light,
    .btn-outline-dark,
    .btn-outline-light-2x {
      color: $white !important;
      border: 1px solid $dark-card-border;
    }

    .btn-outline-light {
      &:hover {
        color: $black !important;
      }
    }

    .dropdown-item {
      color: $dark-all-font-color;
      &:hover {
        background-color: $dark-card-background;
      }
    }
    .blockquote.text-center {
      border: unset;
    }

    .form-control {
      background-color: $dark-body-background;
      color: $dark-all-font-color;
      border: 1px solid $dark-card-border;
    }
    .ui-list{
      .list-group{
        .list-group-item{
          color: $dark-all-font-color;
          border: 1px solid $dark-card-border;
          &.active{
            background-color: $primary-color;
          }
        }
      }
    }
    .search-list{
      .nav-item{
        .nav-link{
          &.active{
            color: $primary-color;
            background-color: rgba($primary-color, 0.1);
          }
        }
      }
    }
    .knowledgebase-search {
      color: $theme-body-font-color;
      .form-inline {
        .form-control {
          background: transparent;
          border-color: transparent;
        }
      }
    }
    .apexcharts-yaxis {
      text {
        fill: $dark-small-font-color;
      }
    }
    .page-wrapper {
      .page-header {
        .header-wrapper {
          .nav-right {
            .cart-dropdown {
              .total {
                background-color: $dark-card-background;
              }
              li {
                .media {
                  .media-body {
                    a {
                      > span {
                        color: $dark-all-font-color;
                      }
                    }
                  }
                }
              }
              .qty-box {
                .input-group {
                  .btn {
                    background-color: $dark-card-background !important;
                    border-color: $dark-card-border !important;
                  }
                }
              }
            }
            .bookmark-flip {
              .bookmark-dropdown {
                .bookmark-content {
                  .bookmark-icon {
                    background-color: $dark-card-background;
                  }
                }
              }
            }
            .notification-dropdown {
              li {
                background-color: $dark-card-background;
              }
            }
          }
        }
      }
    }
    .social-status {
      form {
        .form-group {
          .form-control-social {
            border: 1px solid $dark-card-border;
          }
        }
      }
    }
    .email-wrap {
      .email-right-aside {
        .radius-left {
          background-color: $dark-card-background;
        }
      }
    }
    table.dashed{
      tbody{
        border-top: 2px dashed $dark-card-border !important;
      }
      .dashed{
        border: 2px dashed $dark-card-border;
      }
    }
    table.dotted{
      tbody{
        border-top: 2px dotted $dark-card-border !important;
      }
      .dotted{
        border: 2px dotted $dark-card-border;
      }
    }
    .table > :not(:first-child) {
      border-top: 2px solid $dark-card-border;
    }
    .chat-box{
      .chat-right-aside{
        .chat{
          .chat-header{
            .chat-menu-icons{
              li{
                a{
                  svg{
                    color: $dark-small-font-color;
                  }
                }
              }
            }
          }
        }
      }
    }
    .page-wrapper{
      .page-body-wrapper{
        .page-body{
          .custom-card{
            .card-social{
              li{
                &:hover{
                  a{
                    color: $primary-color;
                  }
                }
              }
            }
          }
        }
      }
    }
    .chart-block{
      #reactgooglegraph-10{
        svg{
          g{
            &:last-child{
              g{
                rect{
                  fill: $dark-body-background;
                }
              }
            }
          }
        }
      }
    }
    .btn-light-gradien{
      color: $theme-body-font-color;
    }
    .taskadd{
      table{
        tbody{
          tr{
            &:last-child{
              td{
                border-bottom: 0;
              }
            }
          }
        }
      }
    }
    .file-content{
      .search-file{
        border: 1px solid $dark-card-border;
        background-color: $dark-body-background;
        .form-control-plaintext.form-control{
          border: 0;
        }
      }
    } 
    .chat-box{
      .people-list{
        .search{
          .form-control {
            background-color: $dark-body-background;
            border: 1px solid $dark-card-border;
          }
        }
      }
    }   
    .product-grid{
      .feature-products{
        .text-end{
          .select-options{
            select.form-control{
              border: 1px solid $dark-card-border;
              background-color: $dark-card-background;
            }
          }
        }
      }
    }
    .product-sidebar{
      .filter-section{
        > .card{
          border-radius: 5px;
        }
      }
    }
    .search-product{
      .form-control{
        background-color: $dark-card-background;
      }
    }
    // react tour css
    .reactour__helper {
      background-color: $dark-body-background;
    }
    .table-responsive{
      table.table{
        border-color: $dark-card-border;
      }
    }
    .table-inverse{
      th, td{
        border-color: $dark-card-border;
      }
    }
    .ecommerce-widget{
      .support-ticket-font{
        .progress{
          background: $dark-body-background;
        }
      }
    }
    .support-table{
      .progress{
        background: $dark-body-background;
      }
    }
    .input-group-text{
      background-color: $dark-card-background;
      border: 1px solid $dark-card-border;
      color: $dark-all-font-color;
    }
    .react-datepicker{
      .react-datepicker__time-container{
        border-left: 1px solid $dark-card-border;
      }
    } 
    .react-datepicker__time-container{
      .react-datepicker__time{
        background: $dark-body-background;
        color: $dark-all-font-color;
      }
    }
    .product-box.features-faq, .product-box.blog-box{
      .product-img{
        .product-hover{
          ul{
            li{
              i{
                color: $dark-all-font-color;
              }
            }
          }
        }
      }
    }
    // modal page
    .modal-content {
      background-color: $dark-card-background;
      border: 1px solid $dark-card-border;
      .modal-header {
        border-bottom: 1px solid $dark-card-border;

        .btn-close {
          color: $dark-small-font-color;
        }
      }
    }
    .modal-footer {
      border-top: 1px solid $dark-card-border;
    }
    .modal {
      .grid-showcase {
        span {
          background-color: $dark-body-background;
          border-color: $dark-card-border;
        }
      }
      .form-control {
        background-color: $dark-body-background;
        border-color: $dark-card-border;
        color: $dark-small-font-color;
      }
    }

    .comingsoon {
      background-blend-mode: overlay;
      background-color: rgba(54, 64, 74, 0.9);

      .comingsoon-inner {
        h5 {
          color: $dark-all-font-color;
        }

        .countdown {
          border-top: 1px solid $dark-card-border;
          border-bottom: 1px solid $dark-card-border;

          .title {
            color: $dark-all-font-color;
          }
        }

        .coming-soon-bottom-link {
          a {
            color: $dark-all-font-color;
          }
        }
      }
    }

    .comingsoon-bgimg,
    .auth-bg-video,
    .auth-bg,
    .auth-bg-video {
      background-color: rgba($dark-body-background, 0.9);
      background-blend-mode: overlay;
    }

    .theme-form {
      .login-divider {
        border-top: 1px solid $dark-card-border;

        &:before {
          background: $dark-card-border;
          color: $dark-all-font-color;
        }
      }
    }

    .authentication-main {
      background-color: $dark-card-border;

      .card {
        box-shadow: 0 0 41px 18px $dark-card-border;
        background-color: $dark-card-background;
      }

      .auth-innerright {
        .reset-password-box {
          .card {
            background-color: $dark-card-background;
          }

          .reset-password-link {
            color: $dark-small-font-color;
          }

          .theme-form {
            .form-group {
              label {
                color: $dark-small-font-color;
              }

              .form-control {
                background-color: $dark-body-background;
                color: $dark-all-font-color;
                border-color: $dark-card-border;
              }
            }

            .opt-box {
              background-color: $dark-card-border;
            }
          }
        }

        .authentication-box {
          h4 {
            color: $dark-all-font-color;
          }

          h6 {
            color: $dark-small-font-color;
          }

          h3 {
            color: $dark-all-font-color;
          }

          .card {
            background-color: $dark-card-background;

            .theme-form {
              .form-group {
                input[type="text"],
                input[type="password"] {
                  background-color: $dark-body-background;
                  border: 1px solid $dark-card-border;
                  color: $dark-all-font-color;
                }
              }

              .checkbox {
                label {
                  &::before {
                    background-color: $dark-body-background;
                    border: 1px solid $dark-card-border;
                  }
                }
              }
            }
          }
        }
      }
    }
    .apexcharts-tooltip.apexcharts-theme-light {
      background-color: $dark-body-background;
      border-color: $dark-card-border;
      .apexcharts-tooltip-title {
        background: $dark-card-background;
        border-bottom: 1px solid $dark-card-border;
        color: $dark-all-font-color;
      }
      .apexcharts-tooltip-series-group {
        color: $dark-all-font-color;
      }
    }
    .vertical-menu-main {
      background-color: $dark-card-background;
    }

    .mega-menu {
      .title {
        color: $dark-all-font-color;
        border-bottom: 1px solid $dark-card-border;
      }

      .list-unstyled {
        div {
          a {
            &:hover {
              color: var(--theme-deafult);
            }
          }
        }
      }
    }

    .default-according {
      .card {
        background-color: $dark-card-background;

        .btn-link {
          background-color: $dark-card-background;
          border: 1px solid $dark-card-background;
          color: $white;
        }

        .text-muted {
          color: $dark-small-font-color !important;
        }
      }

      .bg-primary {
        .btn-link {
          background-color: var(--theme-deafult);
          border: 1px solid var(--theme-deafult);
        }
      }

      .bg-secondary {
        .btn-link {
          background-color: var(--theme-secondary);
          border: 1px solid var(--theme-secondary);
        }
      }
    }
    .search-page{
      .info-block{
        border: 1px solid $dark-card-border;
      }
    } 
    .collapse {
      .card-body {
        background-color: $dark-card-background;
      }
    }
    .react-datepicker {
      background-color: $dark-body-background;
      .react-datepicker__header {
        background-color: $dark-body-background !important;
        border-bottom: 1px solid $dark-card-border;
      }
      .react-datepicker__day-name,
      .react-datepicker__day,
      .react-datepicker__time-name {
        color: $dark-all-font-color;
      }
      .react-datepicker__day,
      .react-datepicker__month-text,
      .react-datepicker__quarter-text,
      .react-datepicker__year-text {
        &:hover {
          background-color: $dark-card-background;
        }
      }
    }
    .page-wrapper.compact-wrapper{
      .page-body-wrapper{
        div.sidebar-wrapper{
          .logo-wrapper{
            position: relative;
            .back-btn{
              position: absolute;
              top: 25%;
              right: 10%;
            }
          }
        }
      }
    }   
    // index page css end
    .ecommerce-widget {
      border: 1px solid $dark-card-border !important;
    }
    .blockquote {
      border-left: 4px solid $dark-card-border;
      &.text-end {
        border-right: 4px solid $dark-card-border;
      }
    }
    .contacts-tabs {
      .nav-pills {
        border-right: 1px solid $dark-card-border;
        .nav-item {
          + .nav-item {
            border-top: 1px solid $dark-card-border;
          }
        }
      }
    }
    .contact-profile {
      .icon-wrapper {
        background-color: $dark-body-background;
      }
    }
    .list-persons {
      .profile-mail {
        .media {
          .media-body {
            ul {
              li {
                + li {
                  border-left: 1px solid $dark-card-border;
                }
              }
            }
          }
        }
      }
    }
    .product-wrapper {
      &.sidebaron {
        .product-sidebar {
          .filter-section {
            .card {
              .left-filter {
                background-color: $dark-card-background;
              }
            }
          }
        }
      }
    }
    #chart-widget9 {
      .apexcharts-marker {
        stroke: $transparent-color;
      }
    }
    #chart-bubble {
      .apexcharts-marker {
        stroke: $transparent-color;
      }
    }
    #chart-widget8,
    #radarchart {
      svg {
        .apexcharts-radar-series {
          polygon {
            stroke: $dark-card-border;
            &:nth-child(odd) {
              fill: $dark-body-background;
            }
            &:nth-child(even) {
              fill: $dark-card-background;
            }
          }
          line {
            stroke: $dark-card-border;
          }
        }
      }
    }

    #chart-widget13,
    #candlestick {
      .apexcharts-tooltip {
        &.apexcharts-theme-light {
          background-color: $dark-body-background;
          border: 1px solid $dark-card-border;
        }
      }
    }
    .apexcharts-gridline {
      stroke: $dark-card-border;
    }
    .apexcharts-tooltip-title,
    .apexcharts-tooltip-series-group,
    .apexcharts-tooltip.light,
    .apexcharts-menu-item {
      color: $black;
    }
    // calender
    .calendar-wrap {
      .fc-unthemed {
        td,
        th {
          border-color: $dark-card-border;
        }
      }
    }
    .rbc-month-view,
    .rbc-time-view {
      border: 1px solid $dark-card-border;
    }
    .rbc-toolbar {
      button {
        color: $dark-small-font-color;
        &:hover,
        &:focus {
          background-color: var(--theme-deafult);
          color: $white;
        }
      }
    }
    .rbc-month-row {
      + .rbc-month-row {
        border-top: 1px solid $dark-card-border;
      }
    }
    .rbc-off-range-bg {
      background: $dark-body-background;
    }
    .rbc-day-bg {
      + .rbc-day-bg {
        border-left: 1px solid $dark-card-border;
      }
    }
    .rbc-header,
    .rbc-timeslot-group {
      border-bottom: 1px solid $dark-card-border;
      + .rbc-header {
        border-left: 1px solid $dark-card-border;
      }
    }
    .rbc-time-content {
      border-top: 2px solid $dark-card-border;
    }
    .rbc-agenda-view {
      table {
        &.rbc-agenda-table {
          border: 1px solid $dark-card-border;
          tbody {
            > tr {
              > td {
                + td {
                  border-left: 1px solid $dark-card-border;
                }
              }
              + tr {
                border-top: 1px solid $dark-card-border;
              }
            }
          }
          thead {
            > tr {
              > th {
                border-bottom: 1px solid $dark-card-border;
              }
            }
          }
        }
      }
    }
    .rbc-day-slot {
      .rbc-time-slot {
        border-top: 1px solid $dark-card-border;
      }
    }
    .rbc-time-header-content,
    .rbc-time-content > * + * > * {
      border-left: $dark-card-border;
    }
    #external-events {
      border: 1px solid $dark-card-border;
    }
    .fc-unthemed {
      th,
      td,
      thead,
      tbody,
      .fc-divider,
      .fc-row,
      .fc-content,
      .fc-popover,
      .fc-list-view,
      .fc-list-heading td {
        border-color: $dark-card-border;
      }
      td {
        &.fc-today {
          background: $dark-body-background;
        }
      }
      .fc-divider,
      .fc-popover,
      .fc-list-heading {
        background-color: $dark-card-border;
        .fc-header,
        td {
          background-color: $dark-card-border;
        }
      }
    }
    .fc-theme-standard {
      td,
      th,
      .fc-scrollgrid {
        border: 1px solid $dark-card-border;
      }
    }
    .apexcharts-xaxistooltip,.apexcharts-yaxistooltip{
      color: $dark-all-font-color;
      background: $dark-body-background;
      border: 1px solid $dark-card-border;
    }
    .contact-form{
      .theme-form{
        .form-control{
          border: 1px solid $dark-card-border;
        }
      }
    }  
    .chart-block{
      > div{
        svg{
          g{
            text{
              fill: $dark-all-font-color;
            }
          }
        }
      }
    }
    .default-datepicker{
      .react-datepicker{
        .react-datepicker__month,.react-datepicker__header{
          background-color: $dark-card-background !important;
        }
      }
    }
    .bottom-content{
      span{
        color: $dark-all-font-color;
      }
    } 
    // typeahead
    .rbt-aux {
      .rbt-close {
        color: $dark-all-font-color;
      }
    }
    .rbt-input-main {
      color: $dark-all-font-color;
    }
    .btn-light.disabled, .btn-light:disabled{
      color: $theme-body-font-color;
    }
    // form wizard
    ol {
      &.progtrckr {
        li {
          span {
            color: $dark-all-font-color;
          }
        }
      }
    }
    // mde editor
    .editor-toolbar {
      border-top: 1px solid $dark-card-border;
      border-left: 1px solid $dark-card-border;
      border-right: 1px solid $dark-card-border;
      a {
        color: $dark-all-font-color !important;
        &:hover,
        &.active {
          background: $dark-card-border;
        }
      }
      i.separator {
        border-left: 1px solid $dark-card-border;
        border-right: 1px solid $dark-card-border;
      }
      button {
        color: $dark-all-font-color;
        &:hover,
        &:focus,
        &.active {
          background: $dark-body-background;
          border-color: $dark-card-border$dark-card-border;
        }
      }
      &.fullscreen {
        &::before,
        &::after {
          background: linear-gradient(
            to right,
            $dark-body-background 0,
            $dark-body-background 100%
          );
        }
      }
    }
    .editor-toolbar.disabled-for-preview {
      a {
        &:not(.no-disable) {
          background: $dark-card-border;
        }
      }
    }
    .editor-preview {
      background-color: $dark-card-background;
    }
    .editor-toolbar.fullscreen,
    .editor-preview-side {
      background: $dark-card-background;
