  &.dark-sidebar {
    .toggle-sidebar {
      top: 27px;
      svg {
        stroke: $dark-all-font-color;
      }
    }
    .page-wrapper {
      &.compact-wrapper{
        .page-body-wrapper{
          div.sidebar-wrapper{
            .sidebar-main{
              .sidebar-links{
                .sidebar-submenu{
                  li{
                    &:hover{
                      a{
                        color: $primary-color;
                      }
                    }
                    a{
                      &.active{
                        color: $primary-color;
                      }
                    }
                  }
                }
                > li{
                  .sidebar-link{
                    &.active{
                      background-color: rgba($primary-color, 0.2);
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
