      &.horizontal-wrapper {
        .page-body-wrapper {
          .sidebar-wrapper {
            background: $dark-card-background;

            .sidebar-main {
              background-color: $dark-card-background;

              .sidebar-links {
                > li {
                  > a {
                    color: $dark-all-font-color;
                  }

                  .sidebar-submenu {
                    background: $dark-card-background;

                    li {
                      a {
                        color: $dark-all-font-color;

                        &.active,
                        &:hover {
                          color: var(--theme-deafult);
                        }
                      }

