                        .link-section {
                          .submenu-title {
                            h5 {
                              color: $dark-all-font-color;
                            }
                          }

                          .submenu-content {
                            &.opensubmegamenu {
                              li {
                                a {
                                  color: $dark-all-font-color;
                                  font-weight: 400;
                                }
                              }
                            }
                          }
                        }
                      }

                      &::after {
                        background-color: $light-all-font-color;
                      }
                    }

                    .sidebar-submenu {
                      li {
                        a {
                          color: $dark-all-font-color;
                        }
                      }
