/**=====================
      90. React-Plugin CSS Start
==========================**/
/**/
.react-kanban-column {
  position: relative;
}

.react-kanban-column-header{
  display: none;
}

.react-kanban-column > div:first-child{
  position: absolute;
  height: 100%;
}

.simple-list {
  .list-group-item {
    padding: 0;
    border: none;
    background-color: transparent;
  }
}
li.list-group-item {
  padding: 0;
  background-color: unset;
  border: unset;
  color: unset;
}
img.media {
  display: unset;
  border: none;
  margin-bottom: 0;
}
ul.list-group {
  display: block;
  li.list-group-item.active {
    background-color: $transparent-color;
    border-color: $transparent-color;
  }
  img {
    width: auto;
    height: auto;
  }
  i {
    margin-right: unset;
  }
}
// slick
.slick-page {
  .slick-slider {
    margin: -8px 0 20px;
  }
}

.slick-slider {
  margin: -7px 0 0;

  .item {
    &:focus {
      outline: none;
    }
  }

  .slick-dots {
    li {
      button {
        &:before {
          font-size: 12px;
          color: var(--theme-deafult);
        }
      }

      &.slick-active {
        button {
          &:before {
            color: var(--theme-deafult);
          }
        }
      }
    }
  }
}

// range slider
.rangeslider-horizontal,
.rangeslider-vertical {
  .rangeslider__fill {
    background-color: var(--theme-deafult) !important;
  }

  .rangeslider__handle {
    &:focus {
      outline: none;
    }
  }
}

.slider-vertical,
.vertical-height {
  .value {
    text-align: center;
  }
}

.rangeslider-vertical {
  .rangeslider__labels {
    margin: 0 0 0 32px !important;
  }
}

.input-range__slider {
  background: var(--theme-deafult);
  border: 1px solid var(--theme-deafult);
}

.input-range__track--active {
  background: var(--theme-deafult);
}

.input-range {
  margin-bottom: 34px;
}

// dropzone
.dzu-dropzone {
  border: 2px dashed var(--theme-deafult) !important;
  background: rgba($primary-color, 0.2);
  overflow: auto !important;

  .dz-message {
    .note {
      background-color: $transparent-color;
    }
  }
}

.dzu-inputLabel {
  color: var(--theme-deafult) !important;
  text-align: center;
  margin-bottom: 0;
}

.dzu-previewContainer {
  padding: 30px !important;
  min-height: 115px;
}

.dzu-inputLabelWithFiles {
  margin-left: 30px !important;
  background-color: var(--theme-deafult) !important;
  color: $white  !important;
}

.dzu-submitButton {
  background-color: var(--theme-deafult) !important;

  &:disabled {
    background-color: #e6e6e6 !important;
  }
}

// Image Cropper
.input-cropper {
  margin-bottom: 15px;
  border: 1px solid #ced4da;
  padding: 10px;
  border-radius: 4px;
  width: auto;

  input {
    width: -webkit-fill-available;
  }
}

.crop-portion {
  margin-left: 0;
  margin-top: 30px;
  vertical-align: baseline;
}

.cropper-view-box {
  outline-color: var(--theme-deafult);
  outline: 1px solid var(--theme-deafult);
}

.cropper-point {
  background-color: var(--theme-deafult);
}

// sticky
.note {
  background-color: rgba($primary-color, 0.3);

  &:nth-child(2n) {
    background: rgba($secondary-color, 0.3);
  }
}

.react-stickies-wrapper {
  .close {

    &:before,
    &:after {
      background: $transparent-color  !important;
    }
  }

  .react-grid-layout {
    .react-grid-item {
      &:nth-child(1n) {
        .note {
          background: lighten($primary-color, 35%) !important;
        }
      }

      &:nth-child(2n) {
        .note {
          background: lighten($secondary-color, 35%) !important;
        }
      }

      &:nth-child(3n) {
        .note {
          background: lighten($success-color, 50%) !important;
        }
      }

      &:nth-child(4n) {
        .note {
          background: lighten($info-color, 30%) !important;
        }
      }

      &:nth-child(5n) {
        .note {
          background: lighten($warning-color, 35%) !important;
        }
      }

      &:nth-child(6n) {
        .note {
          background: lighten($danger-color, 25%) !important;
        }
      }
    }
  }
}

// sweetalert
.lead {
  margin-bottom: 5px;
}

.swal2-container {
  .swal2-actions {
    .swal2-styled {
      font-size: 14px;
    }
  }
}

.swal2-styled {
  &.swal2-confirm {
    background-color: var(--theme-deafult) !important;

    &:focus {
      box-shadow: 0 0 0 1px $white, 0 0 0 3px rgba($primary-color, 0.4);
    }
  }
}

.swal2-title {
  font-size: 20px !important;
}

.swal2-progress-steps {

  .swal2-progress-step,
  .swal2-progress-step-line {
    background: var(--theme-deafult) !important;
  }

  .swal2-progress-step {
    &.swal2-active-progress-step {
      background: var(--theme-deafult) !important;

      ~.swal2-progress-step,
      ~.swal2-progress-step-line {
        background: rgba($primary-color, 0.2) !important;
      }
    }
  }
}

// datetime picker
.bootstrap-datetimepicker-widget {
  tr {

    th,
    td {

      &:active,
      &.active {
        background-color: var(--theme-deafult);
      }

      span {

        &:active,
        &.active {
          background-color: var(--theme-deafult);
        }
      }
    }
  }
}

// upload
.fileContainer {
  padding: 0 !important;
  margin: 0 !important;
  box-shadow: none !important;

  .files-gallery {
    display: flex;
    justify-content: center;

    .files-gallery-item {
      width: 45%;
      display: flex;
      margin: 0 auto;
    }
  }

  .uploadPictureContainer {
    margin: 1% !important;
    padding: 10px;
  }

  .chooseFileButton {
    margin-top: 0 !important;
    padding: 6px 23px;
    background: $secondary-color;
    border-radius: 30px;
    color: $white;
    font-weight: 300;
    font-size: 14px;
    margin: 10px 0;
    transition: all 0.2s ease-in;
    cursor: pointer;
    outline: none;
    border: none;
  }
}

// daterange picker
.daterangepicker {

  td,
  th {
    &.available {
      &:hover {
        background-color: var(--theme-deafult);
      }
    }
  }
}

// to-do
.todo {
  .action-box {
    &.large {
      .icon {

        .icon-trash,
        .icon-check {
          color: var(--theme-deafult);
        }
      }
    }
  }

  .todo-list-wrapper {
    #todo-list {
      li {
        &.completed {
          .task-container {
            .task-label {
              color: var(--theme-deafult);
            }
          }
        }
      }
    }
  }

  @-webkit-keyframes taskHighlighter {
    0% {
      background: rgba($primary-color, 0.5);
    }

    100% {
      background: $white;
    }
  }

  @keyframes taskHighlighter {
    0% {
      background: rgba($primary-color, 0.5);
    }

    100% {
      background: $white;
    }
  }
}

// clock
.react-clock__face {
  border: 1px solid $white  !important;
}

.react-clock__mark__body,
.react-clock__hand__body {
  background-color: $white  !important;
}

// scrollable page
.scrollarea {
  max-width: 800px;
  max-height: 300px;

  .scrollbar-container {
    z-index: 7 !important;
  }
}

//form wizard
ol {
  &.progtrckr {
    list-style-type: none;
    padding: 0;

    li {
      display: inline-block;
      text-align: center;
      line-height: 4.5rem;
      cursor: pointer;

      span {
        padding: 0 1.5rem;
      }

      &.progtrckr-doing {
        color: black;
        border-bottom: 4px solid #cccccc;

        &:before {
          content: "\2022";
          color: white;
          background-color: #cccccc;
          width: 1.2em;
          line-height: 1.2em;
          border-radius: 1.2em;
        }

        &:hover {
          &:before {
            color: #ff4500;
          }
        }
      }

      &.progtrckr-done {
        color: black;
        border-bottom: 4px solid #5cb85c;

        &:before {
          content: "\2713";
          color: white;
          background-color: #5cb85c;
          width: 1.2em;
          line-height: 1.2em;
          border-radius: 1.2em;
        }

        &:hover {
          &:before {
            color: #333;
          }
        }
      }

      &:after {
        content: "\00a0\00a0";
      }

      &:before {
        position: relative;
        bottom: -3.7rem;
        float: left;
        left: 50%;
      }

      &.progtrckr-todo {
        &:before {
          content: "\039F";
          color: silver;
          background-color: $transparent-color;
          width: 1.2em;
          line-height: 1.4em;
        }

        &:hover {
          &:before {
            color: #ff4500;
          }
        }
      }
    }
  }
}

// calender
.rbc-calendar {
  height: 1000px !important;
}

#external-events {
  position: absolute;
  z-index: 2;
  padding: 0 10px;
  border: 1px solid #ccc;
  background: #eee;
  overflow-y: scroll;

  p {
    strong {
      font-size: 16px;
    }
  }
}

#external-events .fc-event {
