@media screen and (max-width: 600px) {
  .jkanban-container {
    .kanban-container {
      .kanban-drag {
        padding: 0;

        >div {
          width: 100% !important;

          >div {
            div[data-rbd-drag-handle-context-id="1"] {
              width: 100% !important;
            }
          }
        }
      }
    }
  }
}

